export default {
  props: {
    id: {
      required: true,
      default: null,
      type: String,
    },
  },
  data: () => ({
    allProducts: '',
    checkbox: '',
    checkbox2: '',
    item: [],
    categories: [
      'Fibre Installation',
      'Klein Tools',
      'Winches and Trailers',
      'Overhead Line',
    ],
    accessoryName: '',
    items: [],
    subCategory: [],
    areFeatures: true,
    areAccessories: true,
    areConfig: true,
    areVideos: true,
    tutorials: '',
    intro: '',
    productId: '',
    images: [],
    preview: null,
    image: '',
    image_list: [],
    headings: ['image', 'name', 'Remove'],
    vidHeadings: ['video', 'name', 'Remove'],
    configHeadings: ['title', 'image', 'table', 'Remove'],
    title: '',
    description: '',
    features: '',
    tab: '',
    videos: '',
    video: '',
    videoTitle: '',
    configImage: '',
    recommendedProducts: [],
    mainImg: '',
    countInStock: 0,
    recommendedProductName: '',
    accessories: '',
    subCategoryNames: [],
    subcategoryItems: [],
    subcategoryItem: '',
  }),
  created() {
    this.getProduct()
    if (this.subcategoryItems.length === 0) {
      this.checkbox = false
    }
  },
  methods: {
    showAccessories() {
      this.data.accesories === []
        ? (this.areAccessories = false)
        : (this.areAccessories = true)

      return this.areAccessories
    },
    async getAllProducts() {
      const config = {
        headers: {
          'Access-Control-Allow-Origin': '*',
        },
      }
      try {
        const response = await this.$axios.get(
          'https://cbsbackend.herokuapp.com/api/products',
          config
        )

        this.allProducts = response.data

        for (const product of response.data) {
          this.items.push(product.name)

          if (product.subCategory.includes(this.title)) {
            this.subcategoryItems.push(product.name)
          }

          if (this.subCategory.includes(product.name)) {
            this.subCategoryNames.push(product.name)
          }
        }

        return this.items
      } catch (err) {
        throw new Error('Error Fetching Products')
      }
    },
    isMobile() {
      if (screen.width <= 760) {
        return true
      } else {
        return false
      }
    },
    async getProduct() {
      const config = {
        headers: {
          'Access-Control-Allow-Origin': '*',
        },
      }
      try {
        const response = await this.$axios.get(
          `https://cbsbackend.herokuapp.com/api/products/${this.id}`,
          config
        )

        this.item.push(response.data)

        // console.log(this.item)

        this.title = this.item[0].name
        this.category = this.item[0].category
        this.productId = this.item[0].productId
        this.intro = this.item[0].intro
        this.images = this.item[0].img
        this.description = this.item[0].description
        this.features = this.item[0].features
        this.mainImg = this.item[0].mainImg
        this.subCategory = this.item[0].subCategory
        this.videos = this.item[0].videos
        this.configurationIntro = this.item[0].configurationIntro
        this.configurationTitle = this.item[0].configurationTitle
        this.configurationImage = this.item[0].configurationImage
        this.tables = this.item[0].tables
        this.accessories = this.item[0].accessories
        this.recommendedProducts = this.item[0].recommendedProducts
        this.mainImg = this.item[0].mainImg
        this.countInStock = this.item[0].countInStock
        this.checkbox = this.item[0].hasSubCategories

        this.getAllProducts()
        return this.item
      } catch (error) {
        throw new Error(error)
      }
    },
    addItem() {
      const config = {
        headers: {
          'Access-Control-Allow-Origin': '*',
        },
      }

      if (this.checkbox) {
        this.productId = ''
      }

      if (this.subcategoryItems.length === 0) {
        this.checkbox = false
      }

      this.allProducts.forEach((item) => {
        if (!this.subcategoryItems.includes(item.name)) {
          item.subCategory.forEach(async (sub, index) => {
            if (sub.toString() === this.title) {
              item.subCategory.splice(index, 1)

              const { data } = await this.$axios.get(
                `https://cbsbackend.herokuapp.com/api/products/name/${item.name}`,
                config
              )

              const response = {
                user: data[0].user,
                name: data[0].name,
                productId: data[0].productId,
                description: data[0].description,
                img: data[0].img,
                mainImg: data[0].mainImg,
                category: data[0].category,
                subCategory: item.subCategory,
                features: data[0].features,
                intro: data[0].intro,
                videos: data[0].videos,
                configurationTitle: data[0].configurationTitle,
                configurationIntro: data[0].configurationIntro,
                configurationImage: data[0].configurationImage,
                tables: data[0].tables,
                countInStock: data[0].countInStock,
                accessories: data[0].accessories,
                recommendedProducts: data[0].recommendedProducts,
                enquiries: data[0].enquiries,
                hasSubCategories: false,
              }

              try {
                await this.$axios.put(
                  `https://cbsbackend.herokuapp.com/api/products/${data[0]._id}`,
                  response,
                  config
                )

                return response
              } catch (error) {
                throw new Error(error)
              }
            }
          })
        }
      })

      this.subcategoryItems.forEach(async (element) => {
        try {
          // console.log(this.accessoryName)
          const { data } = await this.$axios.get(
            `https://cbsbackend.herokuapp.com/api/products/name/${element}`,
            config
          )

          const response = {
            user: data[0].user,
            name: data[0].name,
            productId: data[0].productId,
            description: data[0].description,
            img: data[0].img,
            mainImg: data[0].mainImg,
            category: data[0].category,
            subCategory: this.title,
            features: data[0].features,
            intro: data[0].intro,
            videos: data[0].videos,
            configurationTitle: data[0].configurationTitle,
            configurationIntro: data[0].configurationIntro,
            configurationImage: data[0].configurationImage,
            tables: data[0].tables,
            countInStock: data[0].countInStock,
            accessories: data[0].accessories,
            recommendedProducts: data[0].recommendedProducts,
            enquiries: data[0].enquiries,
            hasSubCategories: false,
          }

          try {
            await this.$axios.put(
              `https://cbsbackend.herokuapp.com/api/products/${data[0]._id}`,
              response,
              config
            )

            return response
          } catch (error) {
            throw new Error(error)
          }
        } catch (error) {
          throw new Error(error)
        }
      })
    },
    async saveImages(event) {
      const input = event.target.files[0]
      const formData = new FormData()

      formData.append('image', input)

      try {
        const config = {
          headers: {
            'Content-Type': 'multipart/form-data',
            'Access-Control-Allow-Origin': '*',
          },
        }

        const { data } = await this.$axios.post(
          'https://cbsbackend.herokuapp.com/api/upload/',
          formData,
          config.headers
        )

        this.image = data

        this.images.push({ image: this.image })
      } catch (error) {
        throw new Error(error)
      }
    },
    async addAccessory() {
      const config = {
        headers: {
          'Access-Control-Allow-Origin': '*',
        },
      }

      try {
        const { data } = await this.$axios.get(
          `https://cbsbackend.herokuapp.com/api/products/name/${this.accessoryName}`,
          config
        )

        this.accessories.push({
          id: data[0].id,
          name: data[0].name,
          category: data[0].category,
          subCategory: data[0].subCategory,
          productId: data[0].productId,
          intro: data[0].intro,
          mainImg: data[0].mainImg,
          _id: data[0]._id,
        })

        return this.accessories
      } catch (error) {
        throw new Error(error)
      }
    },
    deleteAccessory(index) {
      this.accessories.splice(index, 1)
    },
    addSubCategory() {
      if (this.checkbox2) {
        this.subCategoryNames.forEach(async (subCategoryName) => {
          this.subCategory.push(subCategoryName)

          const config = {
            headers: {
              'Access-Control-Allow-Origin': '*',
            },
          }

          try {
            const { data } = await this.$axios.get(
              `https://cbsbackend.herokuapp.com/api/products/name/${subCategoryName}`,
              config
            )

            this.subCategory.push(subCategoryName)

            const response = {
              user: data[0].user,
              name: data[0].name,
              productId: data[0].productId,
              description: data[0].description,
              img: data[0].img,
              mainImg: data[0].mainImg,
              category: data[0].category,
              subCategory: data[0].subCategory,
              features: data[0].features,
              intro: data[0].intro,
              videos: data[0].videos,
              configurationTitle: data[0].configurationTitle,
              configurationIntro: data[0].configurationIntro,
              configurationImage: data[0].configurationImage,
              tables: data[0].tables,
              countInStock: data[0].countInStock,
              accessories: data[0].accessories,
              recommendedProducts: data[0].recommendedProducts,
              enquiries: data[0].enquiries,
              hasSubCategories: true,
            }

            try {
              await this.$axios.put(
                `https://cbsbackend.herokuapp.com/api/products/${data[0]._id}`,
                response,
                config
              )

              return response
            } catch (error) {
              throw new Error(error)
            }
          } catch (error) {
            throw new Error(error)
          }
        })
      } else {
        this.subCategoryNames.forEach(async (subCategoryName) => {
          this.subCategory.push(subCategoryName)

          const config = {
            headers: {
              'Access-Control-Allow-Origin': '*',
            },
          }

          try {
            const { data } = await this.$axios.get(
              `https://cbsbackend.herokuapp.com/api/products/name/${subCategoryName}`,
              config
            )

            this.subCategory.push(subCategoryName)

            const response = {
              user: data[0].user,
              name: data[0].name,
              productId: data[0].productId,
              description: data[0].description,
              img: data[0].img,
              mainImg: data[0].mainImg,
              category: data[0].category,
              subCategory: data[0].subCategory,
              features: data[0].features,
              intro: data[0].intro,
              videos: data[0].videos,
              configurationTitle: data[0].configurationTitle,
              configurationIntro: data[0].configurationIntro,
              configurationImage: data[0].configurationImage,
              tables: data[0].tables,
              countInStock: data[0].countInStock,
              accessories: data[0].accessories,
              recommendedProducts: data[0].recommendedProducts,
              enquiries: data[0].enquiries,
              hasSubCategories: true,
            }

            try {
              await this.$axios.put(
                `https://cbsbackend.herokuapp.com/api/products/${data[0]._id}`,
                response,
                config
              )

              return response
            } catch (error) {
              throw new Error(error)
            }
          } catch (error) {
            throw new Error(error)
          }
        })
      }
    },
    changeSubCategory() {
      const config = {
        headers: {
          'Access-Control-Allow-Origin': '*',
        },
      }

      if (this.checkbox2) {
        this.addSubCategory()
      } else {
        this.subCategory.forEach(async (sub) => {
          try {
            const { data } = await this.$axios.get(
              `https://cbsbackend.herokuapp.com/api/products/name/${sub}`,
              config
            )

            const response = {
              name: data[0].name,
              productId: data[0].productId,
              description: data[0].description,
              img: data[0].img,
              mainImg: data[0].mainImg,
              category: data[0].category,
              subCategory: data[0].subCategory,
              features: data[0].features,
              intro: data[0].intro,
              videos: data[0].videos,
              configurationTitle: data[0].configurationTitle,
              configurationIntro: data[0].configurationIntro,
              configurationImage: data[0].configurationImage,
              tables: data[0].tables,
              countInStock: data[0].countInStock,
              accessories: data[0].accessories,
              recommendedProducts: data[0].recommendedProducts,
              enquiries: data[0].enquiries,
              hasSubCategories: false,
            }

            this.subCategory = []

            try {
              await this.$axios.put(
                `https://cbsbackend.herokuapp.com/api/products/${data[0]._id}`,
                response,
                config
              )

              return response
            } catch (error) {
              throw new Error(error)
            }
          } catch (error) {
            throw new Error(error)
          }
        })
      }
    },
    async addRecommendedProducts() {
      const config = {
        headers: {
          'Access-Control-Allow-Origin': '*',
        },
      }

      try {
        const { data } = await this.$axios.get(
          `https://cbsbackend.herokuapp.com/api/products/name/${this.recommendedProductName}`,
          config
        )

        this.recommendedProducts.push({
          id: data[0].id,
          name: data[0].name,
          category: data[0].category,
          subCategory: data[0].subCategory,
          productId: data[0].productId,
          intro: data[0].intro,
          mainImg: data[0].mainImg,
          _id: data[0]._id,
        })

        return this.recommendedProducts
      } catch (error) {
        throw new Error(error)
      }
    },
    deleteRecommendedProduct(index) {
      this.recommendedProducts.splice(index, 1)
    },
